<template lang="html">
	<div class="page-bar">
		<ul>
			<li v-if="cur!=1"><a v-on:click="btnClick(cur,1)">上一页</a></li>
			<li v-for="index in indexs" v-bind:class="{ active: cur == index}">
				<a v-on:click="btnClick(index,2)">{{ index }}</a>
			</li>
			<li v-if="cur!=all"><a v-on:click="btnClick(cur,3)">下一页</a></li>
			<li><a>共<i>{{all}}</i>页</a></li>
		</ul>
	</div>
</template>

<script>
	import {
		queryOrderList
	} from '../../request/api.js'
	import {mapActions, mapGetters} from 'vuex'
	export default {
		props: {
			cur: {
				type: [String, Number],
				required: true
			},
			all: {
				type: [String, Number],
				required: true
			},
			callback: {
				default () {
					return function callback() {
						// todo
					}
				}
			}

		},
		computed: {
			...mapGetters(['userAll']),
			//动态更新页码，一次只显示当前页面的左右共十个页面
			indexs() {
				var left = 1
				var right = this.all
				var ar = []
				if (this.all >= 11) {
					if (this.cur > 5 && this.cur < this.all - 4) {
						left = this.cur - 5
						right = this.cur + 4
					} else {
						if (this.cur <= 5) {
							left = 1
							right = 10
						} else {
							right = this.all
							left = this.all - 9
						}
					}
				}
				while (left <= right) {
					ar.push(left)
					left++
				}
				return ar
			}
		},
		methods: {
			/**
			 * 分页按钮事件
			 */
			btnClick(page, flag) {
				//上一页，下一页或者直接按了相同的页面
				if (page == this.cur) {
					if (flag == 1) {
						this.queryOrderByPage(--page)
					} else if (flag == 3) {
						this.queryOrderByPage(++page)

					} else {
						return
					}
				} else {
					//直接点击其他页面
					this.queryOrderByPage(page)
				}
			},
			//公用分页事件。返回数据
			queryOrderByPage(page) {
				let request={
					page:page,
					u_id:this.userAll.u_id
				}
				//根据页码查询数据
				queryOrderList(request).then(res => {

					data.list = res
					this.callback(data) //返回给调用组件
				}).catch(err => console.log(err));
			}
		}
	}
</script>

<style lang="css">
	ul,
	li {
		margin: 0px;
		padding: 0px;
	}

	.page-bar {
		text-align: right;
		padding-right: 15%;
	}

	.page-bar li {
		list-style: none;
		display: inline-block;
	}

	.page-bar li:first-child>a {
		margin-left: 0px
	}

	.page-bar a {
		border: 1px solid #ddd;
		text-decoration: none;
		position: relative;
		float: left;
		padding: 6px 12px;
		margin-left: -1px;
		line-height: 1.42857143;
		color: #337ab7;
		cursor: pointer
	}

	.page-bar a:hover {
		background-color: #eee;
	}

	.page-bar .active a {
		color: #fff;
		cursor: default;
		background-color: #337ab7;
		border-color: #337ab7;
	}

	.page-bar i {
		font-style: normal;
		color: #d44950;
		margin: 0px 4px;
		font-size: 12px;
	}
</style>



