import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters.js'
import * as actions from './actions.js'
import * as mutations from './mutations.js'

Vue.use(Vuex) 

//多个组件用到的值放到vuex中管理


//声明全局维护的状态state
const state={
	//个人信息，粉丝关注博客数量
	nav_msg:{								
		attendCount:0,
		fansCount:0,
		blogCount:0
	},
	userAll:{
		u_id: '',
		u_lastname: '',
		u_sex: '',
		u_phone: '',
		u_birth: '',
		u_idcard: '',
		u_status: '',
		u_email: '',
		u_pwd: '',
		username: '',
		address: '',
	},
	token: '',
	picture:""			,//以此类推,
	name:"",
	

}

//注册上面引入的各大模块
const store=new Vuex.Store({
	state,    // 共同维护的一个状态，state里面可以是很多个全局状态
    getters,  // 获取数据并渲染
    actions,  // 数据的异步操作
    mutations  // 处理数据的唯一途径，state的改变或赋值只能在这里
})
export default store


